<template>
    <div>
        <div>
            <el-input prefix-icon="el-icon-plus"
                      style="width: 300px;margin-right: 20px"
                      placeholder="请输入职位"
                      v-model="pos.name"></el-input>
            <el-button type="primary" @click="addPosition">添加职位</el-button>
        </div>
        <div class="postManaMain">
            <el-table
                    :data="positions"
                    border
                    stripe
                    @selection-change="handleSelectionChange"
                    style="width: 70%">
                <el-table-column
                        type="selection"
                        width="55">
                </el-table-column>
                <el-table-column
                        prop="id"
                        label="编号"
                        width="55">
                </el-table-column>
                <el-table-column
                        prop="name"
                        label="职位名称"
                        width="180">
                </el-table-column>
                <el-table-column
                        prop="createDate"
                        label="创建时间"
                        width="150">
                </el-table-column>
                <el-table-column
                        label="是否启用">
                        <template slot-scope="scope">
                            <el-tag size="" type="success" v-if="scope.row.enabled">已启用</el-tag>
                            <el-tag size="" type="danger" v-else>未启用</el-tag>
                        </template>
                </el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button
                                size="mini"
                                @click="showEditView(scope.$index, scope.row)">编辑
                        </el-button>
                        <el-button
                                size="mini"
                                type="danger"
                                @click="handleDelete(scope.$index, scope.row)">删除
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-button @click="deleteMany" type="danger" style="margin-top: 10px" :disabled="multipleSelection.length==0">批量删除</el-button>
        </div>
        <!--修改的对话框-->
        <el-dialog
                title="提示"
                :visible.sync="dialogVisible"
                width="30%">
            <div>
                <div>
                    <el-tag>职位名称</el-tag>
                    <el-input class="updateInput" v-model="updatePos.name"></el-input>
                </div>
                <div>
                    <el-tag style="margin-top: 10px">
                        <el-switch
                        v-model="updatePos.enabled"
                        active-text="启用"
                        inactive-text="禁用">
                        </el-switch>
                    </el-tag>
                </div>
            </div>
            <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="doUpdate">确 定</el-button>
  </span>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "PosMana",
        data() {
            return {
                multipleSelection:[],
                dialogVisible:false,
                updatePos:{
                  name:'',
                  enabled:false,
                },
                pos: {  //添加职位的数据
                    name: ''
                },
                positions: []
            }
        },

        mounted() {
            this.initPositions();
        },
        methods: {
            deleteMany(){
                this.$confirm('此操作将永久删除【'+this.multipleSelection.length+'】条记录, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    let ids='?';
                    this.multipleSelection.forEach(item=>{
                        ids+='ids='+item.id+'&';
                    });
                    this.deleteRequest("/system/basic/pos/"+ids).then(resp=>{
                        if(resp){
                            this.initPositions();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            handleSelectionChange(val){
                this.multipleSelection=val;
            },
            //添加职位
            addPosition() {
                if (this.pos.name) {
                    this.postRequest("/system/basic/pos/", this.pos).then(resp => {
                        if (resp) {
                            this.initPositions();
                            this.pos.name = '';
                        }
                    })
                }else {
                    this.$message.error("职位名不能为空！")
                }
            },
            showEditView(index, data) {
                //this.updatePos=data;
                //使用变量拷贝，防止在编辑时，对话框后面的数据随着动
                Object.assign(this.updatePos,data)
                this.dialogVisible=true;
            },
            doUpdate(){
                this.putRequest("/system/basic/pos/",this.updatePos).then(resp=>{
                    if(resp){
                        this.initPositions();
                        this.updatePos.name='';
                        this.dialogVisible=false;
                    }
                })
            },
            handleDelete(index, data) {
                this.$confirm('此操作将永久删除【' + data.name + '职位】, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequest("system/basic/pos/" + data.id).then(resp => {
                        if (resp) {
                            this.initPositions();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            //初始化职位信息
            initPositions() {
                this.getRequest("/system/basic/pos/").then(resp => {
                    if (resp) {
                        this.positions = resp
                    }
                })
            }
        }
    }
</script>

<style scoped>
    .updateInput{
        width: 200px;
        margin-left: 10px;
    }
    .postManaMain {
        margin-top: 10px;
    }

</style>