<template>
    <div>
        <div>
            <el-input
                    v-model="jl.name"
                    style="width: 300px;"
                    placeholder="添加职称"
            >
            </el-input>
            <el-select  style="margin-left: 10px;margin-right: 10px" v-model="jl.titleLevel" placeholder="职称等级">
                <el-option
                        v-for="item in titleLevels"
                        :key="item"
                        :label="item"
                        :value="item">
                </el-option>
            </el-select>
            <el-button type="primary" @click="addJobLevel" icon="el-icon-plus">添加</el-button>
        </div>
        <div style="margin-top: 10px">
            <el-table
                    :data="jls"
                    @selection-change="handleSelectChange"
                    stripe
                    border
                    style="width: 70%">
                <el-table-column
                        type="selection"
                        width="55">
                </el-table-column>
                <el-table-column
                        prop="id"
                        label="编号"
                        width="55">
                </el-table-column>
                <el-table-column
                        prop="name"
                        label="职称名称"
                        width="150">
                </el-table-column>
                <el-table-column
                        prop="titleLevel"
                        label="职称级别"
                        width="100">
                </el-table-column>
                <el-table-column
                        prop="createDate"
                        label="创建时间">
                </el-table-column>
                <el-table-column
                        label="是否禁用">
                    <template slot-scope="scope">
                        <el-tag size="" type="success" v-if="scope.row.enabled">已启用</el-tag>
                        <el-tag size="" type="danger" v-else>未启用</el-tag>
                    </template>
                </el-table-column>
                <el-table-column
                        label="操作">
                    <template slot-scope="scope">
                        <el-button
                                size="mini"
                                @click="showEditView(scope.row)">编辑
                        </el-button>
                        <el-button
                                size="mini"
                                type="danger"
                                @click="handleDelete(scope.row)">删除
                        </el-button>
                        </template>
                </el-table-column>
            </el-table>
            <el-button type="danger"
                       @click="deleteMany"
                       style="margin-top: 10px"
                       :disabled="multiSelection.length==0">批量删除</el-button>
        </div>
        <el-dialog
                title="修改职称"
                :visible.sync="dialogVisible"
                width="30%">
            <div>
                <table>
                    <tr>
                        <td><el-tag>职称名</el-tag></td>
                        <td><el-input v-model="updateJl.name"></el-input></td>
                    </tr>
                    <tr>
                        <td><el-tag>职称等级</el-tag></td>
                        <td>
                            <el-select  style="margin-left: 10px;margin-right: 10px" v-model="updateJl.titleLevel" placeholder="职称等级">
                            <el-option
                                    v-for="item in titleLevels"
                                    :key="item"
                                    :label="item"
                                    :value="item">
                            </el-option>
                        </el-select>
                        </td>
                    </tr>
                    <tr>
                        <td><el-tag>是否启用</el-tag></td>
                        <td>
                            <el-switch
                                    style="margin-left: 10px"
                                    v-model="updateJl.enabled"
                                    active-color="#13ce66"
                                    inactive-color="#ff4949"
                                    active-text="启用"
                                    inactive-text="禁用">
                            </el-switch>
                        </td>
                    </tr>
                </table>
            </div>
            <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="doUpdate">确 定</el-button>
  </span>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "JobLevelMana",
        data(){
            return{
                dialogVisible:false,
                multiSelection:[],
                titleLevels:[
                    '正高级',
                    '副高级',
                    '中级',
                    '初级',
                    '员级',
                ],
                updateJl:{
                  name:'',
                  titleLevel:'',
                  enabled:false
                },
                jl:{
                    name:'',
                    titleLevel:'',
                    enabled:false
                },
                jls:[]
            }
        },
        mounted() {
            this.initJobLevels();
        },
        methods:{
            deleteMany(){
                this.$confirm('此操作将永久删除【'+this.multiSelection.length+'】条记录, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    let ids ='?';
                    this.multiSelection.forEach(item=>{
                        ids+='ids='+item.id+'&'
                    })
                    this.deleteRequest("/system/basic/joblevel/"+ids).then(resp=>{
                        if(resp){
                            this.initJobLevels();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            handleSelectChange(val){
                this.multiSelection =val;
            },
            addJobLevel(){
                if(this.jl.name&&this.jl.titleLevel){
                   this.postRequest("/system/basic/joblevel/",this.jl).then(resp=>{
                       if(resp){
                           this.initJobLevels();
                           this.jl.name='';
                           this.jl.titleLevel='';
                       }
                   })
                }else{
                    this.$message.error("添加字段不能为空！")
                }
            },
            showEditView(data){
                Object.assign(this.updateJl,data);
                this.dialogVisible=true;
            },
            doUpdate(){
                this.putRequest("/system/basic/joblevel/",this.updateJl).then(resp=>{
                    if(resp){
                        this.initJobLevels();
                        this.dialogVisible=false;
                        this.updateJl.name='';
                    }
                })
            },
            handleDelete(data){
                this.$confirm('此操作将永久删除【'+data.name+'】, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequest("/system/basic/joblevel/"+data.id).then(resp=>{
                        if(resp){
                            this.initJobLevels();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },

            initJobLevels(){
                this.getRequest("/system/basic/joblevel/").then(resp=>{
                    if(resp){
                        this.jls=resp;
                    }
                })
            }
        }
    }
</script>


<style scoped>

</style>