<template>
    <div><!--资源与角色之间的关系-->
        <div class="permissionManaTool">
            <el-input placeholder="请输入角色英文名..." v-model="role.name" style="width: 300px">
                <template slot="prepend">ROLE_</template>
            </el-input>
            <el-input placeholder="请输入角色中文名..." v-model="role.nameZh"></el-input>
            <el-button type="primary" @click="doAddRole">添加角色</el-button>
        </div>
        <div class="permissionManaMain"><!--使用折叠面板展示-->
            <el-collapse v-model="activeName"
                         @change="change" accordion>
                <el-collapse-item :title="r.nameZh" :name="r.id" v-for="(r,index) in roles" :key="index">
                    <el-card class="box-card">
                        <div slot="header" class="clearfix">
                            <span>可访问资源</span>
                            <el-button style="float: right; padding: 3px 0;color: #ff2b1a"
                                       @click="deleteRole(r)"
                                       type="text"
                                       icon="el-icon-delete"></el-button>
                        </div>
                        <div>
                            <el-tree
                                    node-key="id"
                                    ref="tree"
                                    :default-checked-keys="mids"
                                    show-checkbox
                                    :data="allMenus"
                                    :props="defaultProps"></el-tree>
                            <div style="display: flex ;justify-content: flex-end">
                                <el-button size="mini" @click="cancelUpdate">取消修改</el-button>
                                <el-button size="mini" type="primary" @click="doUpdate(r.id,index)">确认修改</el-button>
                            </div>
                        </div>
                    </el-card>
                </el-collapse-item>
            </el-collapse>
        </div>
    </div>
</template>

<script>
    export default {
        name: "PermissMana",
        data(){
            return{
                activeName:-1,
                mids:[],
                roles:[],
                allMenus:[],
                defaultProps: {
                    children: 'children',
                    label: 'name'//对应menu树中的name
                },
                role:{
                    name:'',
                    nameZh:''
                }

            };
        },
        mounted(){
           this.initRoles();
        },

        methods:{
            doAddRole(){
                if(this.role.name&&this.role.nameZh){
                    this.postRequest("/system/basic/permiss/role",this.role).then(resp=>{
                        if(resp){
                            this.role.name='';
                            this.role.nameZh='';
                            this.initRoles();
                        }
                    })
                }else {
                    this.$message.error("数据不可以为空！");
                }
            },
            deleteRole(role){
                this.$confirm('此操作将永久删除【'+role.nameZh+'】, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                   this.deleteRequest("/system/basic/permiss/role/"+role.id).then(resp=>{
                       if(resp){
                           this.initRoles();
                       }
                   })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            cancelUpdate(){
                this.activeName=-1;
            },
            doUpdate(rid,index){
                let tree =this.$refs.tree[index];
                let selectedKeys = tree.getCheckedKeys(true);//true参数设置为叶子节点
                let url = '/system/basic/permiss/?rid='+rid;
                selectedKeys.forEach(key=>{
                    url+='&mids='+key;
                });
                this.putRequest(url).then(resp=>{
                    if(resp){
                        this.activeName=-1;
                    }
                })
            },
            initRoles(){
                this.getRequest("/system/basic/permiss/").then(resp=>{
                    if(resp){
                        this.roles=resp;
                    }
                })
            },
            change(rid){//点开某一项时才调用
                if(rid){
                    this.initAllMenus();
                    this.initSelectedMenus(rid);
                }
            },
            initAllMenus(){
                this.getRequest("/system/basic/permiss/menus/").then(resp=>{
                    if(resp){
                        this.allMenus=resp;
                    }
                })
            },
            initSelectedMenus(rid){
                this.getRequest("/system/basic/permiss/mids/"+rid).then(resp=>{
                    if(resp){
                        this.mids=resp;
                    }
                })
            }
        }
    }
</script>

<style scoped>
    .permissionManaTool{
        display: flex;
    }

    .permissionManaTool .el-input{
        width: 300px;
        margin-right: 10px;
    }
    .permissionManaMain{
        margin-top: 10px;
        width: 800px;
    }

</style>